import { mapState, mapMutations } from 'vuex'
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'
import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
import scan from '@/components/scan/scan.vue'

export default {
	components: {
		uniNavBar,
		uniList,
		uniListItem,
		scan
	},
	data() {
		return {
			title: '',
			scrollTop: 0,
			isShowScan: false
		};
	},
	onLoad(options){},
	computed:{
		...mapState(['hasLogin', 'userInfo'])
	},
	methods: {
		// 退出登录
		...mapMutations(['storeLogout']),
		switchChange(e) {
			uni.showToast({
				title: 'change:' + e.value,
				icon: 'none'
			})
		},
		// 退出登录 清空缓存
		confirmOut() {
			uni.showModal({
				title: '网易云音乐',
				content: '确定退出当前账号吗？',
				cancelColor: '#007AFF',
				success: res => {
					if (res.confirm) {
						this.confirmLogout()
					} else if (res.cancel) {
						console.log('用户点击取消')
					}
				}
			})
		},
		// 确定退出
		confirmLogout() {
			unui.request({
				url: this.$websiteUrl + '/logout',
				method: 'GET',
				data: {},
				success: res => {
					this.storeLogout()
					// 到登录页
					uni.navigateTo({
						url: '/pages/logIn/logIn'
					})
				}
			})
		},
		// 滚动到顶部标题变换
		scroll(e) {
			let scrollTop = e.detail.scrollTop
			if (scrollTop < 5) {
				this.title = ''
				this.scrollTop = 0
			} else {
				this.title = '账号'
			}
		},
		// 打开扫描
		openScan() {
			this.$refs.scan.open()
			this.isShowScan = true
			// 隐藏底部 tabbar
			uni.hideTabBar({})
		},
		// 隐藏扫描
		closeScan() {
			this.$refs.scan.close()
			this.isShowScan = false
			uni.showTabBar({})
		},
		// 扫码成功
		getScanCode(val) {
			this.closeScan()
			uni.showToast({
				icon: 'none',
				title: '扫码成功'
			})
		}
	}
}
